/* 封装的轮播组件 */
<template>
  <div>
    <van-swipe :autoplay="3000" indicator-color="white">
      <van-swipe-item :images="images" v-for="(image, index) in images" :key="index">
        <img v-lazy="image" />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>
<script>
export default {
  nameL: "Swipe",
  props: ["images"],//传props值
  data() {
    return {};
  }
};
</script>
<style lang="scss" scoped>
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
</style>